.button span {
  height: 100% !important;
}

.pdfView {
  display: flex;
}

.divPrincipal {
  flex-direction: row;
}

.memor2 {
  flex-direction: row;
}

.inputmemor1 {
  width: 50%;
}

.inputmemor2 {
  width: 50%;
  margin-top: 0;
  margin-left: 10px;
}

.tamanhoMenor {
  width: 60vw;
}

.tamanhoMenor1 {
  width: 35vw;
}

@media only screen and (max-width: 1000px) {
  .memor2 {
    flex-direction: column;
  }
  .inputmemor1 {
    width: 100%;
  }

  .inputmemor2 {
    width: 100%;
    margin-top: 10px;
    margin-left: 0;
  }
}

@media only screen and (max-width: 500px) {
  .pdfView {
    display: none;
  }
}

@media only screen and (max-width: 1740px) {
  .divPrincipal {
    flex-direction: column;
  }
  .tamanhoMenor {
    width: 100%;
    margin-bottom: 20px;
  }

  .tamanhoMenor1 {
    margin-top: 10px;
    width: 100%;
  }
}